@use 'base/mixins' as m;
@use 'base/variables' as v;



.testimoniales{
    &__heading{
        text-align: center;
        font-family: v.$fuente_principal;
        font-size: 3rem;
    
    }
    @include m.tablet {
        &__grid{
            @include m.grid(5, 2rem);
            margin-bottom: 5rem;
         }
    }
  

    .testimonial{

        background-color: v.$secundario;
        border-radius: 1rem;
        padding: 2rem;
        margin-top: 2rem;

        @include m.tablet {
            margin-top: 0;
        }

        &:nth-child(1){
            grid-row: 1/2;
            grid-column: 1/4;
        }
        &:nth-child(3){
            grid-column: 5/6;
            grid-row: 1/3;
        }
        &:nth-child(2){
            grid-row: 1/2;
            grid-column: 4/5;
        }
        &:nth-child(4){
            grid-row: 2/3;
            grid-column: 1/3;
        }
        &:nth-child(5){
            grid-row: 2/3;
            grid-column: 3/5;
        }

        
        


        &__header{
            display: flex;
            align-items: center;
            
            gap: 2rem;
        }
        &__imagen img{
            border-radius:100%;
            width: 10rem;

        }
        &__autor{
            margin-bottom: 0;
            font-weight: 700;
            color: v.$blanco;
           
        }
        &__texto{
            font-size: 2rem;
            color: v.$blanco;
            margin: 0;
            margin-left: 2rem;
            margin-top: 3rem;

           
        }
        &__texto::before{
            content: '"';
            font-size: 10rem;
            opacity: 0.2;
            position: absolute;
            margin-left: -3rem;
            margin-top: -3rem;
            
        }

        

    }
}




















