@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    display: flex;
    flex-direction: column;
    margin-top: 2rem;

    @include m.desktop {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        
    }
    

    &__logo{
      width: 30rem;
      margin: 0 auto;
      margin-bottom: 5rem;
      @include m.desktop{
        margin-bottom: 0;
      }
    }









}






