@use 'base/mixins' as m;
@use 'base/variables' as v;


.favoritos{
    background-color: v.$grisClaro;
    padding: v.$separacion 0;
    

    &__heading{
        text-align: center;
        padding-top: 5rem;
        color: v.$secundario;
     
    }
    

    @include m.tablet {
        &__grid{
            @include m.grid(2, 5rem)
    
        }
    }
   
    .favorito{
        padding: 3rem;
        border-radius: 1rem;
        background-color: v.$blanco;
        box-shadow: 0px 0px 2.6rem -0.8rem v.$secundario;
        margin-bottom: 3rem;

        &__grid{
           display: flex;
           flex-direction: column;
           align-items: center;
        }
        @include m.tablet {
            margin-bottom: 0;
            &__grid{
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 2rem;
             }
        }


        &__contenido{
            margin-top: 1rem;
        }
        &__contenido img{
            margin: 0 auto;
        }
       

        &__imagen{
            max-width: 100%;
            
         
        }
       
        &__nombre{
            margin-bottom: 0;
            margin-top: 1rem;

            text-align: center;
            color: v.$primario;

        }
        &__descripcion{
            color: v.$secundario;
            line-height: 1.5;
            text-align: center;

        }

        @include m.tablet {
            &__contenido img{
                margin: 0;
            }
            &__descripcion{
                text-align: left;
            }
            &__nombre{
                text-align: left;
            }
        }


    }

}

